import React, { useState } from "react";
import Image from "../pages";
const Register = () => {
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
    experienceLevel: "Fresher",
    city: "Delhi",
    workStatus: "Looking for Job",
  });

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(form);
  };

  return (
    <div className="grid grid-cols-2 ">
      <div className=" bg-[#3D52A2]">
        
      </div>
      <div>
        
        <div className="flex items-center justify-center min-h-screen bg-gray-100 px-4">
          <div className="bg-white p-8 rounded-2xl shadow-md w-full ">
            <h2 className="text-3xl font-semibold text-center text-blue-600 mb-6">
              Register Account
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium">Name</label>
                <input
                  type="text"
                  name="name"
                  value={form.name}
                  onChange={handleChange}
                  className="w-full p-3 border rounded-lg mt-1 focus:outline-none focus:ring-2 focus:ring-blue-400"
                  placeholder="Your full name"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium">Email</label>
                <input
                  type="email"
                  name="email"
                  value={form.email}
                  onChange={handleChange}
                  className="w-full p-3 border rounded-lg mt-1 focus:outline-none focus:ring-2 focus:ring-blue-400"
                  placeholder="example@email.com"
                  required
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium">Password</label>
                  <input
                    type="password"
                    name="password"
                    value={form.password}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg mt-1 focus:outline-none focus:ring-2 focus:ring-blue-400"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium">
                    Confirm Password
                  </label>
                  <input
                    type="password"
                    name="confirmPassword"
                    value={form.confirmPassword}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg mt-1 focus:outline-none focus:ring-2 focus:ring-blue-400"
                    required
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium">
                    Experience
                  </label>
                  <select
                    name="experienceLevel"
                    value={form.experienceLevel}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg mt-1 bg-white"
                  >
                    <option value="Fresher">Fresher</option>
                    <option value="Experienced">Experienced</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium">City</label>
                  <select
                    name="city"
                    value={form.city}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg mt-1 bg-white"
                  >
                    <option value="Delhi">Delhi</option>
                    <option value="Mumbai">Mumbai</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium">Work Status</label>
                <select
                  name="workStatus"
                  value={form.workStatus}
                  onChange={handleChange}
                  className="w-full p-3 border rounded-lg mt-1 bg-white"
                >
                  <option value="Looking for Job">Looking for Job</option>
                  <option value="Open to Offers">Open to Offers</option>
                  <option value="Not Looking">Not Looking</option>
                  <option value="Currently Employed">Currently Employed</option>
                </select>
              </div>

              <button
                type="submit"
                className="w-full bg-blue-600 text-white py-3 rounded-lg font-medium hover:bg-blue-700 transition"
              >
                Register
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
